import Good from '../img/component/Good'
import Ftline from '../img/component/Ftline'

function Ft({id, cls}) {
  return (
    <footer id={id} className={cls}>
       <Ftline></Ftline>
       <div className='container pt-5'>
       
        <div className="d-flex align-items-center justify-content-center">
          <Good />
          <p className='ms-4'>
              A ~ Z     <br></br>
             ~~~!!!
          </p>
        </div>
          
       </div>
    </footer>
    
  )
}

export default Ft